.PageLinks {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.PageLinks-item {
  color: #aaa;
  font-size: 22px;
  cursor: pointer;
  text-decoration: none;
  font-weight: 400;
  -webkit-transition: color 0.2s;
  -o-transition: color 0.2s;
  transition: color 0.2s;
  margin: 16px;
}

.PageLinks-item:hover {
  color: white;
}



