.Logo {
  cursor: pointer;
  text-decoration: none;
  font-weight: 800;
  color: #aaa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 28px;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-transition: color 0.2s;
  -o-transition: color 0.2s;
  transition: color 0.2s;
}

.Logo:hover {
  color: white;
}

.Logo-image {
  width: 60px;
  margin-right: 16px;
  opacity: 0.7;
  -webkit-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.Logo:hover .Logo-image {
  opacity: 1.0;
}



